import type SnippetManager from '../snippetManager';
import { default as createIsTriggerringSnippets } from './isTriggerringSnippets';

export type Queries = ReturnType<typeof createQueries>;

export const createQueries = (manager: SnippetManager) => {
  return {
    isTriggerringSnippets: createIsTriggerringSnippets(manager)
  }
}